import React, { useState } from 'react'

export default function NoteItem(props) {
	const Cap = str => str.charAt(0).toUpperCase() + str.slice(1);
	const tag = props.note.tag ? Cap(props.note.tag) : "General";
	const title = props.note.title ? Cap(props.note.title) : "No Title";
	const description = props.note.description ? Cap(props.note.description) : "No Description";
	const date = new Date(parseInt(props.note.date)).toLocaleString("en-US", { timeZone: 'Asia/Kolkata', dateStyle: "long", timeStyle: "medium", hour12: true })

	const [editedNote, setEditedNote] = useState({ title, description, tag });
	const handleChangeNote = e => setEditedNote({ ...editedNote, [e.target.name]: e.target.value });

	return (
		<>
			<div className="modal fade" id="updateModal" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
				<div className="modal-dialog modal-dialog-centered">
					<div className="modal-content">
						<div className="modal-header">
							<h5 className="modal-title" id="updateModalLabel">Update Modal</h5>
							<button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
						</div>
						<div className="modal-body">
							<div className="mb-3">
								<label htmlFor="title" className="form-label">Enter New Title: </label>
								<input type="text" className="form-control" value={editedNote.title} id="title" name="title" onChange={handleChangeNote} />
							</div>
							<div className="mb-3">
								<label htmlFor="description" className="form-label">Enter New Description: </label>
								<input type="text" className="form-control" value={editedNote.description} id="description" name="description" onChange={handleChangeNote} />
							</div>
							<div className="mb-3">
								<label htmlFor="tag" className="form-label">Enter New Tag: </label>
								<input type="text" className="form-control" value={editedNote.tag} id="tag" name="tag" onChange={handleChangeNote} />
							</div>
						</div>
						<div className="modal-footer">
							<button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
							<button type="button" className="btn btn-dark" data-bs-dismiss="modal" onClick={() => { props.updateNote(props.date, editedNote) }}>Update</button>
						</div>
					</div>
				</div>
			</div>
			<div className="card border-dark mx-3 my-2">
				<div className="d-flex align-item-center justify-content-between card-header border-dark">
					<i className="pe-2 far fa-edit" title="Edit This Note" data-bs-toggle="modal" data-bs-target="#updateModal"></i>
					<div className="text-center fs-5">{tag}</div>
					<i className="ps-2 far fa-trash-alt" onClick={() => { props.deleteNote(props.date) }} title="Delete This Note"></i>
				</div>
				<div className="card-body text-dark">
					<h5 className="card-title">{title}</h5>
					<p className="card-text">{description}</p>
				</div>
				<div className="card-footer border-dark">{date}</div>
			</div>
		</>
	)
}
