import React, { useState, useContext } from 'react';
import AlertContext from "../contexts/alert/alertContext"
import Alert from './Alert';

export default function Signup() {
	const alertCon = useContext(AlertContext);
	const { alert, showAlert } = alertCon;
	const [cred, setCred] = useState({ name: "", email: "", password: "" });

	const fieldChange = e => setCred({ ...cred, [e.target.name]: e.target.value });

	const signup = async (e) => {
		e.preventDefault();
		try {
			const res = await fetch("/auth/signup", {
				method: "POST",
				headers: {
					"content-type": "application/json"
				},
				body: JSON.stringify(cred)
			})
			const json = await res.json();
			if (json.success)
				showAlert("SignUP successful !!", "success");
			else if (json.error) {
				if (json.error.includes("already exsits"))
					showAlert(json.error, "danger");
				else
					throw new Error("Enter Field properly !!");
			}
		}
		catch (error) {
			showAlert(error.message, "danger");
		}
	}


	return (<>
		<Alert alert={alert} />
		<div className="container my-3">
			<h2 className="mb-3">Sign Up</h2>
			<form onSubmit={signup}>
				<div className="mb-3">
					<label htmlFor="name" className="form-label">Name :</label>
					<input type="text" value={cred.name} onChange={fieldChange} className="form-control" id="name" name="name" />
				</div>
				<div className="mb-3">
					<label htmlFor="email" className="form-label">Email address :</label>
					<input type="email" value={cred.email} onChange={fieldChange} className="form-control" id="email" name="email" />
				</div>
				<div className="mb-3">
					<label htmlFor="password" className="form-label">Password :</label>
					<input type="password" value={cred.password} onChange={fieldChange} className="form-control" id="password" name="password" />
				</div>
				<button type="submit" className="btn btn-outline-dark">Sign UP</button>
			</form>
		</div>
	</>
	)
}
