import React from 'react'

export default function Alert(props) {
	if (props.alert)
		return (
			<div className={`alert alert-${props.alert.type} alert-dismissible fade show`} style={{ minHeight: "60px", opacity: "1", transition: "opacity 0.7s" }} role="alert">{props.alert.message}</div>
		)
	else
		return (<div style={{ minHeight: "60px", opacity: "0", transition: "opacity 0.7s" }}></div>)
}