import React, { useContext, useEffect, useState } from 'react'
import NoteItem from './NoteItem';
import { useHistory } from 'react-router-dom';
import AlertContext from "../contexts/alert/alertContext"
import Alert from './Alert';

export default function Notes() {
	const alertCon = useContext(AlertContext);
	const { alert, showAlert } = alertCon;
	const history = useHistory();
	const [notes, setNotes] = useState([]);
	const [note, setNote] = useState({
		title: "",
		description: "",
		tag: ""
	});

	useEffect(() => {
		const fetchNotes = async () => {
			try {
				const res = await fetch(`/notes/fetchallnotes`, {
					method: "GET",
					headers: {
						"Access-Control-Allow-Origin": "http://localhost:3001",
						"Access-Control-Allow-Credentials": true
					},
				})
				const data = await res.json();
				if (data.error && data.error.includes("You are not authorized")) {
					history.push("/login");
				}
				else if (data.error) {
					showAlert(data.error, "danger");
				}
				else if (data.success) {
					setNotes(data.notes);
				}
			}
			catch (error) {
				showAlert(error.message, "danger");
			}
		}

		fetchNotes();
		// eslint-disable-next-line
	}, []);

	const handleChange = e => setNote({ ...note, [e.target.name]: e.target.value });

	const addNote = async (e) => {
		e.preventDefault();
		const data = {};
		data.title = note.title;
		data.description = note.description;
		if (note.tag) data.tag = note.tag;
		try {
			const res = await fetch("/notes/addnewnote", {
				method: "POST",
				headers: {
					"content-type": "application/json",
					"Access-Control-Allow-Origin": "http://localhost:3001",
					"Access-Control-Allow-Credentials": true
				},
				body: JSON.stringify(data)
			})
			const json = await res.json();
			if (json.error && json.error.includes("You are not authorized")) {
				history.push("/login");
			}
			else if (json.error) {
				showAlert(json.error, "danger");
			}
			else if (json.errors) {
				throw new Error(json.errors[0].msg);
			}
			else if (json.success) {
				setNotes(notes.concat({ ...data, date: json.date }));
				showAlert("Note Added!!", "success");
				setNote({ title: "", description: "", tag: "" });
			}
		}
		catch (error) {
			showAlert(error.message, "danger")
		}
	}


	const updateNote = async (date, editedNote) => {
		console.log(editedNote);
		const data = {};
		if (editedNote.title) data.title = editedNote.title;
		if (editedNote.description) data.description = editedNote.description;
		if (editedNote.tag) data.tag = editedNote.tag;
		try {
			const res = await fetch(`/notes/updatenote?time=${date}`, {
				method: "PUT",
				headers: {
					"content-type": "application/json",
					"Access-Control-Allow-Origin": "http://localhost:3001",
					"Access-Control-Allow-Credentials": true
				},
				body: JSON.stringify(data)
			})
			const json = await res.json();
			if (json.error && json.error.includes("You are not authorized")) {
				history.push("/login");
			}
			else if (json.error) {
				showAlert(json.error, "danger");
			}
			else if (json.errors) {
				console.log("in errors");
				throw new Error(json.errors[0].msg);
			}
			else if (json.success) {
				let temp = notes;
				const index = temp.findIndex(note => note.date === date);
				temp[index].title = data.title;
				temp[index].description = data.description;
				if (editedNote.tag) temp[index].tag = data.tag;
				setNotes(temp);
				showAlert("Note Updated!!", "success");
			}
		}
		catch (error) {
			showAlert(error.message, "danger")
		}
	}

	const deleteNote = async (date) => {
		try {
			const res = await fetch(`/notes/deletenote?time=${date}`, {
				method: "DELETE",
				headers: {
					"Access-Control-Allow-Origin": "http://localhost:3001",
					"Access-Control-Allow-Credentials": true
				},
			})
			const json = await res.json();
			if (json.error && json.error.includes("You are not authorized")) {
				history.push("/login");
			}
			else if (json.error) {
				showAlert(json.error, "danger");
			}
			else if (json.success) {
				let temp = notes;
				temp.splice(temp.findIndex(note => note.date === date), 1);
				setNote(temp);
				showAlert("Note Deleted!!", "warning");
			}
		}
		catch (error) {
			showAlert(error.message, "danger");
		}
	}

	return (<>
		<Alert alert={alert} />
		<div className="container my-3">
			<h2>Add Note</h2>
			<form onSubmit={addNote}>
				<div className="mb-3">
					<label htmlFor="title" className="form-label">Enter Title :</label>
					<input type="text" className="form-control" value={note.title} id="title" name="title" onChange={handleChange} required minLength={3} />
				</div>
				<div className="mb-3">
					<label htmlFor="description" className="form-label">Enter Description :</label>
					<input type="text" className="form-control" value={note.description} id="description" name="description" onChange={handleChange} required minLength={5} />
				</div>
				<div className="mb-3">
					<label htmlFor="tag" className="form-label">Enter Tag :</label>
					<input type="text" className="form-control" id="tag" value={note.tag} name="tag" onChange={handleChange} />
				</div>
				<button type="submit" className="btn btn-dark">Add</button>
			</form>
			<div className="d-flex flex-row justify-content-center align-items-center flex-wrap my-3">
				{notes.length > 0 ? notes.map(note => <NoteItem key={note.date} handleChange={handleChange} updateNote={updateNote} date={note.date} note={note} deleteNote={deleteNote} />) : <h5>No Note to show !!</h5>}
			</div>
		</div>
	</>
	)
}
