import React, { useState, useContext } from 'react';
import AlertContext from "../contexts/alert/alertContext"
import { useHistory } from 'react-router-dom';
import Alert from './Alert';

export default function Login() {
	const alertCon = useContext(AlertContext);
	const { alert, showAlert } = alertCon;
	const [cred, setCred] = useState({ email: "", password: "" });
	const history = useHistory();
	const fieldChange = e => setCred({ ...cred, [e.target.name]: e.target.value });

	const login = async (e) => {
		e.preventDefault();
		try {
			const res = await fetch("/auth/login", {
				method: "POST",
				headers: {
					"content-type": "application/json",
					"Access-Control-Allow-Origin": "http://localhost:3001",
					"Access-Control-Allow-Credentials": true
				},
				body: JSON.stringify(cred)
			})
			const json = await res.json();
			if (json.success) {
				showAlert("Logged In !! You Will be redirected in few second", "success");
				setTimeout(() => {
					history.push("/");
				}, 3500);
			}
			else if (json.errors) {
				throw new Error(json.errors[0].msg);
			}
			else if (json.error && json.error.includes("Authentication Failed !!")) {
				throw new Error(json.error);
			}
		}
		catch (err) {
			showAlert(err.message, "danger");
		}
	}
	return (
		<>
			<Alert alert={alert} />
			<div className="container my-3">
				<h2 className="mb-3">Login</h2>
				<form onSubmit={login}>
					<div className="mb-3">
						<label htmlFor="email" className="form-label">Email address :</label>
						<input type="email" value={cred.email} onChange={fieldChange} className="form-control" id="email" name="email" />
					</div>
					<div className="mb-3">
						<label htmlFor="password" className="form-label">Password :</label>
						<input type="password" value={cred.password} onChange={fieldChange} className="form-control" id="password" name="password" />
					</div>
					<button type="submit" className="btn btn-outline-dark">Login</button>
				</form>
			</div>
		</>
	)
}
