import React from 'react';
import { Link, useLocation, useHistory } from 'react-router-dom';

export default function Navbar() {
	let location = useLocation();
	const history = useHistory();
	const getCookie = cname => {
		let name = cname + "=";
		let decodedCookie = decodeURIComponent(document.cookie);
		let ca = decodedCookie.split(';');
		for (let i = 0; i < ca.length; i++) {
			let c = ca[i];
			while (c.charAt(0) === ' ') {
				c = c.substring(1);
			}
			if (c.indexOf(name) === 0) {
				return c.substring(name.length, c.length);
			}
		}
		return "";
	}

	const logout = () => {
		document.cookie = "isloggedin=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
		document.cookie = "authtoken=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
		history.push("/login");
	}

	return (
		<nav className="navbar navbar-expand-lg navbar-dark bg-dark">
			<div className="container-fluid">
				<Link className="navbar-brand" to="/">Notes-App</Link>
				<button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
					<span className="navbar-toggler-icon"></span>
				</button>
				<div className="collapse navbar-collapse" id="navbarSupportedContent">
					<ul className="navbar-nav me-auto mb-2 mb-lg-0">
						<li className="nav-item">
							<Link className={`nav-link ${location.pathname === "/" ? "active" : ""}`} aria-current="page" to="/">Home</Link>
						</li>
						<li className="nav-item">
							<Link className={`nav-link ${location.pathname === "/about" ? "active" : ""}`} to="/about">About</Link>
						</li>
						{/* <li className="nav-item dropdown">
							<Link className="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
								Dropdown
							</Link>
							<ul className="dropdown-menu" aria-labelledby="navbarDropdown">
								<li><Link className="dropdown-item" to="#">Action</Link></li>
								<li><Link className="dropdown-item" to="#">Another action</Link></li>
								<li><hr className="dropdown-divider" /></li>
								<li><Link className="dropdown-item" to="#">Something else here</Link></li>
							</ul>
						</li> */}
					</ul>
					{getCookie("isloggedin") !== "true" ?
						<div className="d-flex">
							<Link className="btn btn-sm btn-outline-light me-2" to="/signup">Signup</Link>
							<Link className="btn btn-sm btn-outline-light" to="/login">Login</Link>
						</div> :
						<div className="d-flex">
							<button onClick={logout} className="btn btn-sm btn-outline-light me-2">Logout</button>
						</div>
					}
				</div>
			</div>
		</nav>
	)
}
